* {
  box-sizing: border-box; }

.container {
  display: flex;
  border: 8px solid #34495e;
  flex-wrap: wrap; }

.box {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  text-align: center;
  padding: 10px;
  /* width: 1000px; */
  flex-grow: 1;
  min-width: 300px; }

.box-1 {
  background: #16a085; }

.box-2 {
  background: #f1c40f; }

.box-3 {
  background: #e74c3c; }
